@import "globals/colors";
@import "globals/variables";
@import "globals/mixins";


body {
    background-color: $white;
    position: relative;
  }
  
  * {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  
  html,
  body {
    height: 100%;
  }
  
  h1, h2, h3, h4, h5, h6 {
    font-family: $bebas;
  }
  
  li, a, p {
    font-family: $inter;
  }
  
  //Animations
  $bezier: cubic-bezier(0.64, 0.04, 0.35, 1);
  .to-reveal {
    opacity: 0;
    position: relative;
    top: 20px;
  }
  
  .revealed {
    transition: 0.6s $bezier;
    opacity: 1;
    position: relative;
    top: 0;
  }
  
  .container-margins {
    @include container-margins;
   }
  
   .dark-bg {
    transition: 300ms;
    visibility: hidden;
   }
  
   .show-dark-bg {
    background-color: rgba($black, 0.9);
    position: fixed;
    top: 0;
    left: 0;
    z-index: 9999;
    width: 100%;
    height: 100%;
    visibility: visible;
   }
  
   [aria-current="page"] {
      color: $black !important;
  }


